import React, { useRef } from "react";
import useSlider from "../hooks/useSlider";
import { useHistory } from "react-router-dom";
import { Link } from "react-router-dom";
import { stringify } from "javascript-stringify";

const Slider = ({ images }) => {
  const slideImage = useRef(null);
  const slideText = useRef(null);
  const slideLink = useRef(null);
  /*==const slideLink = useRef(null)
  
  <Link to="link2"><button className="feature__btn">More Info</button></Link>
  --*/

  const { goToPreviousSlide, goToNextSlide } = useSlider(
    slideImage,
    slideText,
    slideLink,
    images
  );

 

  const FancyButton = React.forwardRef((props, ref) => (
    <button
            innerRef={slideLink}
            ref={slideLink}
            className="button"
            type="button"
            onClick={(e) => {
              e.preventDefault();
              window.location.href = slideLink.current.innerText;
            }}
          >
      {props.children}
    </button>
    
  ));



  return (
    <div className="slider" ref={slideImage}>
      <link href="css/normalize.css" rel="stylesheet" type="text/css" />
      <link href="css/webflow.css" rel="stylesheet" type="text/css" />
      <link
        href="css/dominique-northecide-portfolio-2020.webflow.css"
        rel="stylesheet"
        type="text/css"
      />
      {/* [if lt IE 9]><![endif] */}
      <link
        rel="apple-touch-icon"
        sizes="180x180"
        href="/apple-touch-icon.png"
      ></link>
      <link
        rel="icon"
        type="image/png"
        sizes="32x32"
        href="/favicon-32x32.png"
      ></link>
      <link
        rel="icon"
        type="image/png"
        sizes="16x16"
        href="/favicon-16x16.png"
      ></link>
      <link rel="manifest" href="/site.webmanifest"></link>

      <div className="slider--content">
        <button onClick={goToPreviousSlide} className="slider__btn-left">
          <i className="fas fa-angle-left"></i>
        </button>
        <div className="slider--feature">
          <p ref={slideText} className="feature--text font-weight-bold"></p>

          
          <FancyButton>Hi</FancyButton>
        </div>
        <button onClick={goToNextSlide} className="slider__btn-right">
          <i className="fas fa-angle-right"></i>
        </button>
      </div>
    </div>
  );
};

export default Slider;
